* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html, body {
    width: 100%;
    max-width: 100%;
    height: 100%;
}
body {
    background: url(/mentor-dashboard/static/media/bg.3509ceab.jpg) no-repeat #4D5150;
    background-position: top center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
ul {
    list-style: none;
}
li {
    display: -webkit-flex;
    display: flex;
    border-bottom: 1px solid #202C3A;
    -webkit-align-items: center;
            align-items: center;
}
#root {
    height: 100%;
}
.app {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100%;
}
.appBody {
    display: -webkit-flex;
    display: flex;
    margin: 0 auto;
    overflow-x: auto;
    max-width: 98%;
}
.appHeader {
    width: 50%;
    max-width: 500px;
    min-width: 300px;
    margin: 80px auto 30px;
}
.tasksList {
    min-width: 15rem;
}
.tasksList li {
    height: 2rem;
    background: rgba(168, 181, 198, 0.5);
}
.tasksList a {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    padding-left: 1rem;
    -webkit-align-items: center;
            align-items: center;
    color: #10161D;
    text-decoration: none;
}
.tasksList a[data-type = 'yes']:hover {
    color: #C2C5CB;
}
.studentsList {
    display: -webkit-flex;
    display: flex;
}
.student {
    padding: 0;
    -webkit-flex-direction: column;
            flex-direction: column;
    border: 0;
}
.student ul {
    width: 100%;
}
.student li {
    height: 2rem;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
}
.student li a {
    text-decoration: none;
    color: #10161D;
}
.student li a:hover {
    font-weight: bold;
}
.studentName {
    display: -webkit-flex;
    display: flex;
    height: 2rem;
    padding: 0.5rem;
    border-bottom: 1px solid #202C3A;
    white-space: nowrap;
    color: #DFF0E8;
    text-shadow: 1px 1px #4D5150;
}

.tasksList .emptyCell {
    height: 2rem;
    background: transparent;
}
.statusToDo {
    background: rgba(85, 85, 85, 0.7);
}
.statusInProgress {
    background: rgba(232, 232, 38, 0.7);
}
.statusDone {
    background: rgba(64, 149, 64, 0.7);
}
.statusChecked {
    background: rgba(178, 35, 35, 0.7);
}
.statusChecking {
    background: rgba(209, 117, 123, 0.7);
}
.entrance {
    display: -webkit-flex;
    display: flex;
    height: 100%;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    padding-bottom: 70px;
}
.fileLinks {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.fileLinks li{
    height: 200px;
    width: 150px;
    margin-left: 50px;
    border: 0;
}
.fileLinks li:first-child{
    margin-left: 0;
}
.fileLinks a {
    display: -webkit-flex;
    display: flex;
    height: 100%;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    background-image: url(/mentor-dashboard/static/media/file-icon.6ad09f1b.svg);
    background-repeat: no-repeat;
    background-position: center;
    text-decoration: none;
    color: #F2F2F2;
}
.fileLinks a:hover {
    background-image: url(/mentor-dashboard/static/media/file-icon-hover.31348bc4.svg);
    color: #D3FDAA;
}

/* css button from https://codepen.io/42EG4M1/pen/epmzBP */

.btn {
  position: relative;
  width: 200px;
  height: 40px;
  margin: 0 auto;
  line-height: 40px;
  text-align: center;
  cursor: pointer;
}

.btn::before {
  content: '';
  position: absolute;
  top: -100px;
  left: 0;
  right: 0;
  width: 1px;
  height: 80px;
  margin: auto;
  background: #fff;
  visibility: hidden;
  opacity: 0;
}

.btn:hover::before {
  -webkit-animation: sword .5s;
          animation: sword .5s;
}

@-webkit-keyframes sword {
  0% {top: -100px; visibility: hidden; opacity: 0;}
  20% {visibility: visible; opacity: 1;}
  100% {top: 100px; visibility: hidden; opacity: 0;}
}

@keyframes sword {
  0% {top: -100px; visibility: hidden; opacity: 0;}
  20% {visibility: visible; opacity: 1;}
  100% {top: 100px; visibility: hidden; opacity: 0;}
}

.btn span {
  display: block;
  color: #fff;
  font-size: 14px;
  text-decoration: none;
}

.btn span::before,
.btn span::after {
  content: '';
  position: absolute;
  top: 0;
  width: 100px;
  height: 38px;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  -webkit-transition: all .5s 0s;
  transition: all .5s 0s;
}

.btn span::before {
  left: 0;
  border-left: 1px solid #fff;
}

.btn span::after {
  right: 0;
  border-right: 1px solid #fff;
}

.btn span:hover::before {
  left: -20px;
  -webkit-transition: all .5s .2s;
  transition: all .5s .2s;
  opacity: 0;
}

.btn span:hover::after {
  right: -20px;
  -webkit-transition: all .5s .2s;
  transition: all .5s .2s;
  opacity: 0;
}

